import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-portfolio-page-article',
  templateUrl: './portfolio-page-article.component.html',
  styleUrls: ['./portfolio-page-article.component.css']
})
export class PortfolioPageArticleComponent implements OnInit {
  currArticle:string = ""
  constructor(private router:Router) { }

  ngOnInit() {
    if(this.router.url.startsWith("/portfolio/")){
    switch (this.router.url) {
      case "/portfolio/fixit":  this.currArticle = "fixit";break;
      case "/portfolio/admtc":  this.currArticle = "admtc";break;
      case "/portfolio/amdi":  this.currArticle = "amdi";break;
      case "/portfolio/resumely":  this.currArticle = "resumely";break;
      case "/portfolio/farmavans":  this.currArticle = "farmavans";break;
      case "/portfolio/internspace":  this.currArticle = "internspace";break;
      default : this.router.navigateByUrl("/portfolio")
    }
  }
}
  

}
